import axios from 'axios'

const baseUrl = 'https://cnodejs.org/api/v1'


export default {
  getTopics (params) {
    return (params.tab) ? axios.get(`${baseUrl}/topics?tab=${params.tab}`) : axios.get(`${baseUrl}/topics`)
  },
  getTopic (params){
    return axios.get(`${baseUrl}/topic/` + params.id)
  },
  postToken (params){
    return axios.post(`${baseUrl}/accesstoken`, params)
  },
  getUserInfo (params){
    return axios.get(`${baseUrl}/user/` + params.userName)
  },
  replies (params,topic_id){
    return axios.post(`${baseUrl}/${topic_id}/replies`, params)
  }


}



